/* dm-serif-display-regular - latin */
@font-face {
	font-family: 'DM Serif Display';
	font-style: normal;
	font-weight: 400;
	// src: url('../fonts/dm-serif-display-v3-latin-regular.eot'); /* IE9 Compat Modes */
	src: local('DM Serif Display Regular'), local('DMSerifDisplay-Regular'),
		//  url('../fonts/dm-serif-display-v3-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
		 url('../fonts/dm-serif-display-v3-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
		 url('../fonts/dm-serif-display-v3-latin-regular.woff') format('woff'), /* Modern Browsers */
		//  url('../fonts/dm-serif-display-v3-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
		//  url('../fonts/dm-serif-display-v3-latin-regular.svg#DMSerifDisplay') format('svg'); /* Legacy iOS */
  }
  
/* dm-serif-display-italic - latin */
@font-face {
	font-family: 'DM Serif Display';
	font-style: italic;
	font-weight: 400;
	// src: url('../fonts/dm-serif-display-v3-latin-italic.eot'); /* IE9 Compat Modes */
	src: local('DM Serif Display Italic'), local('DMSerifDisplay-Italic'),
		//  url('../fonts/dm-serif-display-v3-latin-italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
		 url('../fonts/dm-serif-display-v3-latin-italic.woff2') format('woff2'), /* Super Modern Browsers */
		 url('../fonts/dm-serif-display-v3-latin-italic.woff') format('woff'), /* Modern Browsers */
		//  url('../fonts/dm-serif-display-v3-latin-italic.ttf') format('truetype'), /* Safari, Android, iOS */
		//  url('../fonts/dm-serif-display-v3-latin-italic.svg#DMSerifDisplay') format('svg'); /* Legacy iOS */
  }
  
